import React from "react";
import { DARK_THEME, LIGHT_THEME } from "../state/actions/theme.action";
import { connect } from "react-redux";

const withTheme = Component => {
  const mapStateToProps = state => ({
    theme: state.theme
  });

  const mapDispatchToProp = dispatch => ({
    setDarkTheme: () => dispatch({ type: DARK_THEME }),
    setLightTheme: () => dispatch({ type: LIGHT_THEME })
  });

  const WrappedComponent = props => (
    <Component {...props}>{props.children}</Component>
  );

  return connect(mapStateToProps, mapDispatchToProp)(WrappedComponent);
};

export default withTheme;
