import React from "react";
import styled from "styled-components";
import ReactPlayer from "react-player";
const BigView = props => {
  const { next, item } = props;
  return (
    <Container>
      <Background>
        <VideoBack url={item.video} muted playing width="100%" />
      </Background>
      <ReactPlayer
        url={item.video}
        muted
        playing
        height="100%"
        width="80%"
        onEnded={next}
        style={{ zIndex: 3 }}
      />
    </Container>
  );
};

const VideoBack = styled(ReactPlayer)`
  video {
    height: 600px !important;
    filter: blur(40px);
    opacity: 0.4;
  }
`;

const Background = styled.div`
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 1;
`;

const Container = styled.div`
  flex: 3;
  justify-content: center;
  padding-top: 20px;
  align-items: center;
  display: flex;
  height: 530px;
  position: relative;
`;

export default BigView;
